 * @Descripttion: 角色管理列表
 * @version: 
 * @Author: 曾元球
 * @Date: 2019-06-20 09:05:19
 * @LastEditors: 曾元球
 * @LastEditTime: 2019-08-09 10:26:53
 -->
<template>
  <div class="page flex-v">
    <div class="head flex-v-center flex-justify">
      <div class="head-title">角色维护</div>
      <!--  @click="$router.push({name: 'PersonList'})" -->
      <div class="head-btn"
        v-if="btns.hasInsert"
        data-btn-id='106003001'
        @click="openDialog('insert')">
        <img src="@/assets/img/add.png"
          alt="登记">&nbsp;<span>新增</span>
      </div>
    </div>
    <div class="query-form">
      <el-form inline
        size='small'
        class="default-form-style">

        <el-form-item label='角色：'>
          <el-input placeholder="请输入"
            v-model="querys.roleName"
            clearable></el-input>
        </el-form-item>
        <el-form-item label='所属机构名称：'>
          <el-select v-model="querys.createOrganizationId"
            clearable>
            <el-option v-for="item in orgList"
              :key='item.sysGuid'
              :value="item.sysGuid"
              :label="item.oname" />
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button icon="el-icon-search"
            round
            @click="$refs.pagination.initPageList(1)">查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="table-box flex-item">
      <el-table :data='tableData'
        height='100%'
        header-cell-class-name='custom-table-cell'
        v-loading='loading'
        stripe>
        <el-table-column label='序号'
          type='index'>
          <template v-slot='{$index}'>
            {{$index + (pageNum - 1) * pageSize + 1}}
          </template>
        </el-table-column>
        <el-table-column label='机构名称'
          prop="orgName"
          show-overflow-tooltip></el-table-column>
        <el-table-column label='角色'
          prop='roleName'></el-table-column>
        <el-table-column label='角色状态'
          prop='status'>
          <template v-slot="{row}">
            <span :class="[row.status == 1 ? 'status-enable' : 'status-disable']"></span>
          </template>
        </el-table-column>
        <el-table-column label='权限'>
          <template v-slot="{row}">
            <a class="link"
              @click="jumpAssign(row)">关联权限</a>
          </template>
        </el-table-column>

        <el-table-column label='操作'>
          <template v-slot="{row}">
            <i class="iconfont bj-search"
              @click="openDialog('see', row)"></i>
            <i class="iconfont bj-edit"
              v-if="btns.hasUpdate&&row.status==1"
              data-btn-id='106003002'
              @click="openDialog('update', row)"></i>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="foot">
      <bj-pagination queryUrl='/role/getRoleByCondition'
        :queryObj='querys'
        @listResult='getListResult'
        @controlLoading='loading=$event'
        ref="pagination" />
    </div>

    <el-dialog :visible.sync="dialogIsShow"
      custom-class='my-dialog'
      class="flex"
      width='500px'
      :title="dialogTitles[dialogType]"
      :close-on-click-modal='false'
      center>
      <template v-if='dialogType==="see"'>
        <div class="flex">
          <el-form size="small"
            class="see-style"
            label-width="auto">
            <el-form-item label="角色名称：">
              {{dataInfo.roleName}}
            </el-form-item>
            <el-form-item label="是否启用：">
              {{dataInfo.status == 1 ? '是' : '否'}}
            </el-form-item>
            <el-form-item label="备注：">
              {{dataInfo.remark | emptyData}}
            </el-form-item>
          </el-form>
        </div>
        <div slot='footer'
          v-if="btns.hasUpdate"
          data-btn-id='106003002'>
          <el-button class="btn-info"
            @click="openDialog('update')">修改</el-button>
        </div>
      </template>
      <template v-else>
        <el-form size="small"
          label-width="auto"
          :model="formFields"
          :rules="rules"
          ref='form'>
          <el-form-item label="角色名称："
            prop="roleName">
            <el-input placeholder="请输入角色名称"
              v-model="formFields.roleName"
              maxlength="11"></el-input>
          </el-form-item>
          <el-form-item label="是否启用："
            prop="status">
            <el-select v-model="formFields.status"
              class="block"
              placeholder="启用">
              <el-option value="1"
                label="启用"></el-option>
              <el-option value="2"
                label="禁用"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="备注："
            prop="remark">
            <el-input type="textarea"
              v-model="formFields.remark"
              rows='4'
              resize='none'
              maxlength="201"></el-input>
          </el-form-item>
        </el-form>
        <div slot='footer'>
          <el-button class="btn-hollowing"
            @click="dialogIsShow = false">取消</el-button>
          <el-button class="btn-info"
            @click="onSubmit"
            :loading="submitLoading">保存</el-button>
        </div>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { encryptedPwd, decryptPwd, lenRule } from '@/lib/utils'
import { mapState, mapMutations } from 'vuex'
export default {
  name: 'RoleList',
  data() {
    let createOrganizationId = this.$store.getters.o_userinfo.orgGuid
    return {
      tableData: [],
      querys: {
        roleName: '',
        createOrganizationId
      },
      pageNum: 1,
      pageSize: 10,

      formFields: {
        roleId: '',
        roleName: '',
        status: '1',
        remark: ''
      },
      rules: {
        roleName: [
          {
            required: true,
            message: '请输入角色名称',
            trigger: 'blur',
            whitespace: true
          },
          ...lenRule(10)
        ],
        status: { required: true, message: '请选择是否启用' },
        remark: { max: 200, message: '最多输入200个字符' }
      },
      dataInfo: {},
      dialogIsShow: false,
      dialogType: '',
      loading: true,
      submitLoading: false
    }
  },

  created() {
    this.dialogTitles = { insert: '新增', update: '修改', see: '查看' }
    // this.$post('/role/removeOneRole', {roleId:'340959953676144640'})
  },

  methods: {
    getListResult({ list, pageNum, pageSize }) {
      this.tableData = list
      this.pageNum = pageNum
      this.pageSize = pageSize
    },
    openDialog(type, data) {
      this.dialogIsShow = true
      this.dialogType = type
      if (data) {
        this.dataInfo = data
        Object.keys(this.formFields).forEach(key => {
          this.formFields[key] = data[key]
        })
        this.formFields.roleId = data['roleId']
      } else if (type === 'insert') {
        Object.keys(this.formFields).forEach(key => {
          this.formFields[key] = ''
          if (key === 'roleId') {
            delete this.formFields[key]
          }
        })
        this.formFields.status = '1'
      }
      this.dialogType !== 'see' && this.$nextTick(() => this.$refs.form.clearValidate())
    },

    onSubmit() {
      this.$refs.form.validate(valid => {
        if (!valid) return
        this.submitLoading = true
        let url = `/role/${this.dialogType}OneRole`
        let msg = this.dialogTitles[this.dialogType]
        this.$post(url, this.formFields)
          .then(res => {
            this.submitLoading = false
            this.dialogIsShow = false
            this.$refs.pagination.initPageList()
            this.$message.success(`${msg}成功`)
          })
          .catch(err => {
            this.submitLoading = false
            this.$message.error(err)
          })
      })
    },
    jumpAssign({ roleId, roleName }) {
      this.$router.push({
        name: 'AssignPermissions',
        params: { roleId, roleName }
      })
    }
  },

  computed: {
    btns() {
      let btnIds = this.$store.state.btnAuthorityIds
      return {
        hasInsert: ~btnIds.indexOf('106003001'), // 登记按钮权限
        hasUpdate: ~btnIds.indexOf('106003002'), // 修改按钮权限
        hasDel: ~btnIds.indexOf('106003003') // 删除按钮权限
      }
    },
    ...mapState(['orgList'])
  }
}
</script>

<style lang="less" scoped>
.head {
  padding: 0 70px 0 20px;
  height: 70px;
  border-bottom: 1px solid #eaeff5;
  &-title {
    font-size: 24px;
  }
  &-btn {
    line-height: 30px;
    color: #466791;
    cursor: pointer;
    img,
    span {
      vertical-align: middle;
      font-size: 16px;
    }
  }
}
.el-form.see-style {
  margin: 0 auto;
  max-width: 90%;
}
</style>



